import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axiosInstance from "../baseurl";
import { ContextWrap } from "../App";

function MediaProfile() {

  const url=useContext(ContextWrap)

  
  let navigate = useNavigate();
  const [plan, setplan] = useState({
    planid: { name: "" },
    expiry: "",
  });
  const [data, setdata] = useState({
    name: "",
    contact: "",
    email: "",
    regNo: "",
    web: "",
    address: "",
    img: { filename: "" },
  });

  useEffect(() => {
    if (localStorage.getItem(`medialogid`) == null) {
      navigate(`/home`);
    }
  },[]);

  useEffect(() => {
    axiosInstance
      .post(`/viewMediaById/${localStorage.getItem("medialogid")}`)
      .then((res) => {
        console.log(res, "media data");
        if (res.data.data != undefined) {
          setdata(res.data.data);
        }
      })
      .catch((err) => {
        console.log(err);
      });

    axiosInstance
      .post(`/viewSubByMId/${localStorage.getItem("medialogid")}`)
      .then((res) => {
        console.log(res, "view sub by Media id");
        if (res.data.data != undefined) {
          setplan(res.data.data);
        }
      })
      .catch((err) => console.log(err));
  }, []);

  const changefn = (e) => {
    if (e.target.name == "img") {
      setdata({ ...data, img: e.target.files[0] });
    } else {
      setdata({ ...data, [e.target.name]: e.target.value });
    }
  };
  const subfn = (e) => {
    e.preventDefault();
    axiosInstance
      .post(`/editMediaById/${localStorage.getItem("medialogid")}`, data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((res) => {
        console.log(res);
        if (res.data.status == 500) {
          alert(res.data.msg);
        } else {
          console.log("Submitted", res);
          alert(res.data.msg);
          navigate("/home");
        }
      })
      .catch((err) => {
        console.log("Error", err);
      });
  };

  return (
    <div className="main" style={{ minHeight: "400px" }}>
      <h2 style={{ textAlign: "center" }}>
        {" "}
        Hi {data.name}. Welcome to your profile.
      </h2>
      <hr />
      <div className="container">
        <div className="row">
          <div className="col-5">
            <div class="card card-body">
              <div className="main">
                <div class="card">
                  <div class="card-body">
                    <h1 class="card-title">
                      <span>
                        {" "}
                        <img
                          height={80}
                          width={80}
                          src={`${url}/${data.img.filename}`}
                        />{" "}
                      </span>
                      {data.name}{" "}
                      {data.verified ? (
                        <span>
                          {" "}
                          <img
                            height={100}
                            src="data:image/png;base64,"
                          />{" "}
                        </span>
                      ) : null}
                    </h1>
                    <a href={data.web}>
                      {" "}
                      <span>
                        {" "}
                        <img
                          src="https://cdn-icons-png.flaticon.com/512/1006/1006771.png"
                          height={20}
                        />
                      </span>{" "}
                      {data.web}
                    </a>
                    <h4 class="card-text">Email : {data.email}</h4>
                    <h4> Contact : {data.contact}</h4>
                  </div>
                </div>
              </div>
            </div>
            <div class="card card-body">
            <div className="main">
                {plan.length ? (
                  plan.map((a) => {
                    return (
                      <div class="card">
                        <div class="card-body">
                          <h1 class="card-title">Plan : {a.planid.name}</h1>
                          <h3 class="card-text">
                            <span class="badge bg-secondary">
                              Expiry Date : {a.expiry.slice(0, 10)}
                            </span>
                          </h3>
                        </div>
                       
                      </div>
                    );
                  })
                ) : (
                  <div class="card-body">
                    <h1 class="card-title">No Subscribed Plans</h1>
                    <Link
                      class="btn btn-danger"
                      aria-current="page"
                      to="/Media/Plan"
                    >
                      Choose a Plan
                    </Link>
                  </div>
                )}
              </div>
            </div>
          </div>
          <div className="col-7">
            <div>
              <div class="page-wrapper font-poppins">
                <div className="formcontainer">
                  <div class="wrapper wrapper--w680">
                    <div class="card card-4">
                      <div class="card-body">
                        <h2 class="title">Edit your Profile</h2>
                        <form onSubmit={subfn}>
                          <div class="row row-space">
                            <div class="col-12">
                              <div class="input-group">
                                <label class="label"> Name</label>
                                <input
                                  class="input--style-4"
                                  type="text"
                                  name="name"
                                  value={data.name}
                                  onChange={changefn}
                                  required
                                />
                              </div>
                            </div>
                            <div class="col-12">
                              <div class="input-group">
                                <label class="label"> Contact</label>
                                <input
                                  class="input--style-4"
                                  type="tel"
                                  minLength="10"
                                  maxLength="10"
                                  name="contact"
                                  value={data.contact}
                                  onChange={changefn}
                                  required
                                />
                              </div>
                            </div>
                            <div class="col-12">
                              <div class="input-group">
                                <label class="label"> Email</label>
                                <input
                                  class="input--style-4"
                                  type="email"
                                  name="email"
                                  value={data.email}
                                  onChange={changefn}
                                  required
                                />
                              </div>
                            </div>
                          </div>
                          <div class="col-12">
                            <div class="input-group">
                              <label class="label">Website </label>
                              <input
                                class="input--style-4"
                                type="url"
                                name="web"
                                value={data.web}
                                onChange={changefn}
                                required
                              />
                            </div>
                          </div>
                          <div class="col-12">
                            <div class="mb-3">
                              <label
                                for="exampleFormControlTextarea1"
                                class="form-label"
                              >
                                Address
                              </label>
                              <textarea
                                class="form-control"
                                name="address"
                                onChange={changefn}
                                required
                                value={data.address}
                                id="exampleFormControlTextarea1"
                                rows="3"
                              ></textarea>
                            </div>
                          </div>
                          <div class="col-12">
                            <div class="mb-3">
                              <label for="formFile" class="form-label">
                                Upload your Image
                              </label>
                              <input
                                class="form-control"
                                type="file"
                                id="formFile"
                                name="img"
                                onChange={changefn}
                                
                              />
                            </div>
                          </div>
                          <div class="p-t-15">
                            <button class="btn btn-primary" type="submit">
                              Submit
                            </button>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default MediaProfile;
